import CountUp from "react-countup";
import React from "react";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import blue from "@material-ui/core/colors/blue";
import {
  AcheivementsContainer,
  AcheivementsText,
  AcheivementsContainerWave
} from "./AcheivementsElements";
import { makeStyles } from "@material-ui/core/styles";
import WaveBody from "../Wave/index2";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(3),
      width: theme.spacing(32)
    }
  },
  container: {
    display: "flex"
  },

  customBorderRadius: {
    borderRadius: 25,
    border: `3px solid ${blue[200]}`,
    height: 200,
    flex: 1,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    elevation: 5,
    margin: 30,
    minWidth: 200,
    Color: "#f1f4f5"
  }
}));

const Acheivements = () => {
  const classes = useStyles();
  return (
    <AcheivementsContainerWave>
      <AcheivementsContainer>
        <AcheivementsText>
          <h1>उपलब्धियाँ</h1>
          <div className={classes.container} justifyContent="center">
            <Grid container spacing={16} justify="flex-start">
              <Grid item xs={12} sm={6} md={4} lg={4}>
                <Paper className={classes.customBorderRadius} elevation={15}>
                  <Box p={1}>
                    <CountUp end={120000} duration={5} delay={3} />
                    <Typography variant="h5">मछली पालक</Typography>
                  </Box>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={4} lg={4}>
                <Paper className={classes.customBorderRadius} elevation={15}>
                  <Box p={2}>
                    <CountUp end={250000} duration={5} delay={3} />
                    <Typography variant="h5">
                      मीट्रिक टन गुणवत्तापूर्ण फीड
                    </Typography>
                  </Box>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6} md={4} lg={4}>
                <Paper className={classes.customBorderRadius} elevation={15}>
                  <Box p={1}>
                    <CountUp end={35} duration={5} delay={3} />
                    <Typography variant="h5">विशेषज्ञ</Typography>
                  </Box>
                </Paper>
              </Grid>
            </Grid>
          </div>
        </AcheivementsText>
      </AcheivementsContainer>
      <WaveBody />
    </AcheivementsContainerWave>
  );
};

export default Acheivements;
